import { Component, State, Prop, h } from '@stencil/core';

@Component({
  tag: 'nav-bar',
  styleUrl: 'nav-bar.css',
  shadow: true,
})
export class NavBar {
  @State() drop: boolean = false
  @Prop() first: string
  @Prop() second: string
  @Prop() third: string
  @Prop() fourth: string
  @Prop() firstPath: string
  @Prop() secondPath: string
  @Prop() thirdPath: string
  @Prop() fourthPath: string




  render() {
    let dropdown;
    if (this.drop) {
      dropdown = <ul class="dropdown-list">
        <a href={this.firstPath}><li>{this.first}</li></a>
        <a href={this.secondPath}><li>{this.second}</li></a>
        <a href={this.thirdPath}><li>{this.third}</li></a>
        <a href={this.fourthPath}><li>{this.fourth}</li></a>
      </ul>
    }
    return (
      <div class='nav'>
        <div class='logo'>JOURNAL</div>
        <div class='list-div'>
          <ul class="nav-list">
            <a href={this.firstPath}><li>{this.first}</li></a>
            <a href={this.secondPath}><li>{this.second}</li></a>
            <a href={this.thirdPath}><li>{this.third}</li></a>
            <a href={this.fourthPath}><li>{this.fourth}</li></a>
          </ul>
        </div>
        <div class='dropdown-div'>
          <svg onClick={() => this.drop = !this.drop} xmlns="http://www.w3.org/2000/svg" height="1em" viewBox="0 0 448 512"><path d="M0 96C0 78.3 14.3 64 32 64H416c17.7 0 32 14.3 32 32s-14.3 32-32 32H32C14.3 128 0 113.7 0 96zM0 256c0-17.7 14.3-32 32-32H416c17.7 0 32 14.3 32 32s-14.3 32-32 32H32c-17.7 0-32-14.3-32-32zM448 416c0 17.7-14.3 32-32 32H32c-17.7 0-32-14.3-32-32s14.3-32 32-32H416c17.7 0 32 14.3 32 32z" /></svg>

          {dropdown}
        </div>
      </div>
    );
  }

}
